@import url(https://fonts.googleapis.com/css2?family=Marck+Script&display=swap);
body {
 margin: 0;
 font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
 -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale;
 /*custom css*/
 font-family: "Marck Script", cursive;
 background-color: #5c6ac4;
 background-image: url("https://images.pexels.com/photos/4993245/pexels-photo-4993245.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
 background-position: center;
 background-repeat: no-repeat;
 background-size: cover;
 background-attachment: fixed;
}
#main-nav {
 background-color: #1c1b19;
 font-family: "Markazi Text", serif;
}

#main-nav ul,
#main-nav ul li a {
 color: rgb(241, 228, 228);
}

#article-page {
 background-color: #070000ad;
 color: #ece4df;
 font-size: 24px;
 text-indent: 5mm;
}

.article-list-component-container .text {
 background-color: #fffff7;
 color: rgb(161, 11, 11);
}
.article-list-component-container a:hover {
 text-decoration: none;
}

.article-content {
 background-color: rgba(226, 225, 181, 0.87);
 color: black;
}
#article-page .otherArticles {
 font-size: 16px;
}

.App {
 text-align: center;
}

.App-logo {
 height: 40vmin;
 pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
 .App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
 }
}

.App-header {
 background-color: #282c34;
 min-height: 100vh;
 display: -webkit-flex;
 display: flex;
 -webkit-flex-direction: column;
         flex-direction: column;
 -webkit-align-items: center;
         align-items: center;
 -webkit-justify-content: center;
         justify-content: center;
 font-size: calc(10px + 2vmin);
 color: white;
}

.App-link {
 color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
 from {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
 }
 to {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
 }
}

@keyframes App-logo-spin {
 from {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
 }
 to {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
 }
}

